/*
 * Стили компонента Modal
 */

.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1200;
}

.Modal {
  /* max-width: 90vw;
  max-height: 90vh; */
  border: 2px solid azure;
  background-color: azure;
}
